
body{background-color:#EDEDED
}
#app{margin-top:30px
}
hr{border:1px solid #ddd;margin:18px 0
}
hr{border:1px solid #ddd;margin:18px 0
}
.tokens{margin:-4px
}
.token{padding:8px 16px;margin:4px;background-color:#013649;color:#fff;display:inline-block;line-height:16px;border-radius:32px;cursor:pointer
}
.title-input{width:100%;max-width:400px;padding:8px 0;outline:none;font-size:2em;border:none;background-color:transparent
}
.title-input:focus{border-bottom:1px solid #444
}
.sexp-box,.drag-source-container{margin:0 -15px;display:block;padding:8px 15px
}
.sexp-box>.sexp-label,.drag-source-container>.sexp-label{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;margin-bottom:16px
}
